@import "../../assets/css/config/variables";
@import "../../assets/css/mixin/function";

.error {
  max-width: 1440px;
  margin: 0 auto;
  @include sp {
    max-width: 100%;
    padding: get_sp(20);
  }

  .inner {
    padding: 205px 41px 230px 135px;
    display: flex;
    @include sp {
      padding: get_sp(20);
      text-align: left;
      flex-direction: column-reverse;
    }

    .txt {
      display: block;
      max-width: 488px;
      margin-right: 136px;
      @include sp {
        min-width: 100%;
        margin-right: 0;
      }

      h2 {
        font-size: 96px;
        font-weight: 600;
        color: $clr-base-font;
      }

      p {
        font-size: 48px;
        font-weight: 400;
        color: $clr-base-font;
        margin-bottom: 20px;
      }

      span {
        font-size: 28px;
        line-height: 38px;
        font-weight: 400;
        color: $clr-error;
      }

      a {
        text-decoration: none;

        button {
          margin-top: 66px;
          width: 384px;
          height: 97px;
          font-size: 48px;
          line-height: 65px;
          font-weight: 600;
          background-color: $background-error;
          color: $clr-base-font;
          border: 1px solid $border-error;
          border-radius: 20px;
          @include sp {
            width: 100%;
          }

          &:hover {
            box-shadow: 4px 4px 4px 2px rgba(193, 186, 186, 0.25);
          }
        }
      }
    }

    .thumb {
      width: 640px;
      height: 590px;
      @include sp {
        margin-bottom: 30px;
        width: 310px;
        height: 310px;
      }

      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}
